@import "@/styles/main.scss";




































































































































.stats-container{
    background-color: #F6F4FA;
    background-size: cover;
    padding: 100px 25px;

    .stats-holder{
        width: 25%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        .icon-holder{
            width: 100% ;
            display: flex;
            justify-content: center;
            align-items: center;

            .stats-icon{
                width: 86px;
                border-radius: 100px;
                padding: 20px;
                height: 86px;
                background-color: #055FDC;

            }
        }

        .number-holder{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 25px 0 0px;

            .number{
                font-family: 'Roboto', sans-serif !important ;
                color: #E8496D;
                font-size: 4.5rem;
                font-weight: 200 !important;
            }
        }

        .description-holder{
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            margin-top: -10px;
            padding: 0;

            .des{
                margin: 0;
                padding: 0;
                color: #555555;
                font-size: 1.1rem;
                font-weight: 600;
            }
        }
    }
}

@media (max-width: 900px){
    .stats-container{
        flex-wrap: wrap;

        .stats-holder{
            width: 50%;
            margin-bottom: 80px;
        }
    }
}

@media (max-width: 550px){
    .stats-container{
        flex-direction: columns;

        .stats-holder{
            width: 100%;
            margin-bottom: 80px;
        }
    }
}
