@import "@/styles/main.scss";










































































































    .about-our-team-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 150px 49px 20px;
        color: #000000;

        .about-our-team{

            .about-our-team-text{
                width: 50%;
                padding: 30px 0px 130px;

                .about-our-team-text-heading{
                    width: 100%;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-start;

                    h3{
                        color: #000000 !important;
                        font-size: 2.4rem;
                        font-weight: 700;
                        margin-bottom: 0;
                        cursor: default;

                        span{
                            color: #E8496D;
                        }
                    }

                    p{
                        margin-top: 3px;
                        color: #A9A9A9;
                        font-weight: 100;
                        font-size: 1.5rem;
                        cursor: default;
                    }
                }
                    p{
                        width: 100%;
                        text-align: left !important;
                        direction: ltr !important;
                        cursor: default;
                        display: flex;
                        justify-content: flex-start !important;
                        align-items: center;
                        margin-top: 15px;
                        color: #676767;
                        font-size: 1rem;
                        line-height: 2rem;
                        font-weight: 400!important;
                    }

                .circle-check-container{

                    >div{
                        width: 50%;
                        align-items: flex-start;

                        >div{
                            width: 100%;
                            display: flex;
                            justify-content: flex-start;
                            align-items: center;
                            margin-bottom: 10px;
                            position: relative;

                            p{
                                font-size: 1.1rem;
                                font-weight: 600;
                                margin: 0;
                                padding: 0;
                                margin-left: 42px;
                                cursor: default;
                            }

                            svg{
                                position: absolute;
                                left: 0;
                                top: 0;
                                transition: all 0.2s ease;
                            }

                            &:hover{

                                svg{
                                    font-size: 2.5rem;
                                    left: -5px;
                                    top: -5px;
                                }
                            }

                        }
                    }
                }
            }

            .about-our-team-img{
                width: 50%;
                padding: 20px;
                position: relative;

                .about-our-team-img-holder{
                    position: relative;

                    img{
                        width: 100%;
                        border-radius: 6px;
                    }

                    .red-dot-back-top{
                        position: absolute;
                        bottom: -40px;
                        right: -20px;
                        height: 95%;
                        width: 95%;
                        background-image: url(~@/assets/red-dot.png);
                        background-size: 3%;
                        background-repeat: repeat;
                        z-index: -100000;
                    }

                    .red-dot-back-bottom{
                        position: absolute;
                        bottom: -35px;
                        left: -15px;
                        height: 95%;
                        width: 95%;
                        background-image: url(~@/assets/red-dot.png);
                        background-size: 3%;
                        background-repeat: repeat;
                        z-index: -100000;
                    }

                }

            }
        }
    }

.check-circle svg{
    color: #055FDC !important;
}

@media (max-width: 950px){
    .about-our-team{
        flex-direction: column;

        .about-our-team-text{
            width: 100% !important;
        }

        .about-our-team-img{
            width: 100% !important;

            .red-dot-back-top{
                bottom: -35px !important;
            }
        }
    }
}

@media (max-width:500px ){
    .about-our-team-container{
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 20px 21px 20px !important;
    }
    .about-our-team{
        flex-direction: column;

        .about-our-team-text{
            width: 100% !important;
            padding-left: 0 !important;
            padding-right: 0 !important;
        }

        .about-our-team-img{
            width: 100% !important;

            .red-dot-back-top{
                bottom: -35px !important;
            }
        }

        .circle-check-container{
            margin-top: 40px;
            flex-direction: column !important;

            >div{
                width: 100% !important;

                >div{
                    margin-bottom: 15px !important;
                }
            }

        }
    }
}
