@import "@/styles/main.scss";
















































.skill-container{

    padding: 50px 0 ;

    .skill-left-section{
        width: 55%;
        padding: 20px 10px 20px 49px;
        justify-content: center;
        align-items: flex-start;

        h3{
            color: #010101;
            font-size: 2.5rem;
            font-weight: 700;
            margin-bottom: 10px;

            span{
                color: #E8496D;
                margin-left: 5px;
                font-size: 2.5rem;
                font-weight: 700;

            }
        }

        span{
            color: #9B9B9B;
            font-size: 1.3rem;
            font-weight: 400;
            text-align: left ;
            margin-bottom: 40px;

        }

        p{
            text-align: left;
            color: #6A6A6A;
            font-size: 1rem;
            font-weight: 500;
            line-height: 2rem;
        }
    }

    .skill-right-section{
        width: 45%;
        padding: 40px 30px 40px 10px;

        .skill-holder{
            padding: 10px 0;
            width: 100%;
            justify-content: center;
            align-items: flex-start;
            overflow-x: hidden;
            .skill-title{
                width: 100%;
                justify-content: flex-start;
                color: #010101;
                text-align: left !important;
                font-size: 1rem;
                font-weight: 500;
            }

            .skill-bar{
                // margin-left: 170px;
                height: 2px;
                width: 100%;
                background-color: #E8E8E8;
                display: flex;

                .skill-progress{
                    margin-left: -10px;
                    animation-duration: 4s;
                    height: 2px;
                    display: flex;
                    background-color: #E8496D;
                    background-image: -webkit-gradient(linear, left top, left bottom, from(#f674a4), to(#e06995));
                    background-image: -webkit-linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,109,1) 65%, rgba(0,111,138,1) 100%);
                    background-image: -moz-linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,109,1) 65%, rgba(0,111,138,1) 100%);
                    background-image: -ms-linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,109,1) 65%, rgba(0,111,138,1) 100%);
                    background-image: -o-linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,109,1) 65%, rgba(0,111,138,1) 100%);
                    background-image: linear-gradient(90deg, rgba(255,0,0,1) 0%, rgba(121,9,109,1) 65%, rgba(0,111,138,1) 100%);

                }
            }
        }
    }
}

@media (max-width: 768px){
    .skill-container{
        flex-direction: column;

        .skill-left-section{
            width: 100%;
        }

        .skill-right-section{
            width: 100%;
            padding-left: 50px;
        }
    }
}

@media (max-width: 568px){
    .skill-container{
        flex-direction: column;

        .skill-left-section{
            width: 100%;
            padding-left: 21px !important;
        }

        .skill-right-section{
            width: 100%;
            padding-left: 21px;
        }
    }
}
